* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    font-family: "Montserrat", sans-serif;
}

@media print {
  @page {
    margin: 0;
  }
}

ul,
ol {
    list-style: none;
}
p {
  font-size: 14px;
  line-height: 20px;
}
a:link {
  color: rgb(138, 138, 138);
  text-decoration: none;
  transition: .3s all;
}
a:visited {
  color: rgb(192, 192, 192);
}
a:hover{
  color: rgb(73, 73, 73);
}
// Header and Footer
.header,
.footer {
    width: auto;
    height: 70px;
    background-color: #fff;

    @media print {
      display: none;
    }
}

.footer {
    box-shadow: 0px 5px 20px 0 rgba(0, 0, 0, 0.15);
}

.header__container,
.footer__container {
    width: 320px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
    @media all and (min-width: 768px) {
      width: 80%;
      max-width: 80%;
    }
}

.logo {
    display: block;
    width: 110px;
    height: 20px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: top left;
    filter: grayscale(100%);
}

// Main
.main {
    position: relative;
    &::before {
        position: absolute;
        top: 0;
        content: '';
        width: 100%;
        height: 300px;
        background-color: #efefef;
    }
}

.preview__wrapper {
    padding: 100px 0 150px;
    max-width: 320px;
    margin: 0 auto;
    position: relative;
    @media all and (min-width: 768px) {
      width: 80%;
      max-width: 80%;
    }

    @media print {
      margin: 0;
      padding: 0;
      min-width: 100%;
    }
}

.preview__print-btn {
    display: none;
    width: 50px;
    height: 50px;
    background-color: #000;
    background-image: url(images/print.svg);
    color: #fff;
    border: 0;
    border-radius: 2px;
    position: fixed;
    right: 10%;
    font-family: "Montserrat", sans-serif;
    font-weight: 900;
    letter-spacing: 1px;
    outline: none;
    cursor: pointer;
    transition: .3s all;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .15);
    &:hover {
        background-color: #9C9C9C;
    }
    @media all and (min-width: 768px) {
      display: block;
    }

    @media print {
      display: none;
    }
}

.cv__wrapper {
    background-color: #fff;
    box-shadow: 0px 9px 71px -21px rgba(0, 0, 0, 0.3);
    border-radius: 2px;
    margin: 0px auto;
    padding: 40px 20px;
    @media all and (min-width: 768px) {
      padding: 50px 170px;
    }

    @media print {
      margin: 0;
      padding: 40px;
      width: 100%;
      box-shadow: none;
    }
}

.author__image {
    width: 150px;
    height: 150px;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    border-radius: 50%;
    box-shadow: 0px 15px 20px 0 rgba(0, 0, 0, 0.1);
    margin: 0 auto 30px;

    @media print {
      -webkit-print-color-adjust: exact;
      color-adjust: exact;
    }
}

.author__name {
    font-size: 40px;
    font-weight: 900;
    text-align: center;
}

.profile__roles-list {
    margin: 10px auto 50px;
    text-align: center;
}

.author__role {
    font-size: 20px;
    font-weight: 100;
    text-transform: uppercase;
    line-height: 25px;
}
.author-data__container {
  display: flex;
  justify-content: space-between;
}
.author__birthday,
.author__yearsOfExperience {
  margin-bottom: 5px;
  font-size: 12px;
  line-height: 16px;
}
.author__yearsOfExperience {
  width: 100px;
  text-align: right;
}
.publicLinks__wrapper {
  margin: 30px 0 45px;
  border-top: 1px solid rgb(231, 231, 231);
  padding-top: 30px;
}
.publicLinks__list {
  padding-left: 10px;
}
.publicLinks__list-item {
  position: relative;
}
.publicLinks__list-anchor, .link {
  font-size: 12px;
  line-height: 14px;
  padding-left: 8px;
  margin: 10px;
  display: block;
  position: relative;
  word-wrap: break-word;
  &::before {
    content: '';
    width: 9px;
    height: 9px;
    background-image: url(images/link.svg);
    position: absolute;
    left: -5px;
    top: 2px;
    transition: .5s all;
  }
  &:hover::before {
    transform: rotate(90deg);
  }
}
.wrapper {
  margin: 30px 0 70px;
  border-top: 1px solid rgb(231, 231, 231);
  padding-top: 30px;
}
.title {
  text-transform: uppercase;
  margin-bottom: 30px;
  font-size: 25px;
}
.paragraph {
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 20px;
}
.significant-exp__list {
  list-style: square inside;
}
.sig-relations__item {
  margin-bottom: 30px;
}
.sig-relations__name {
  font-weight: 700;
  font-size: 18px;
  margin-bottom: 15px;
}
.sig-relations__role {
  color: rgb(102, 102, 102);
  margin: 5px;
}
.sig-relations__company {
  margin-left: 5px;
  font-weight: 700;
  font-size: 12px;
  color: rgb(73, 73, 73);
}

// Company

.company__item {
  margin-bottom: 70px;
}
.company__name {
  font-size: 20px;
  color: rgb(151, 151, 151);
  text-transform: uppercase;
  font-weight: 900;
}
.role-list__role-name {
  color: #565656;
}
.role-list__date {
  font-size: 12px;
  color: #999;
}
.role-list__wrapper {
  margin: 10px 0 30px 15px;
}
.challengesActions__list {
  margin-left: 10px;
}
.challenges__item {
  margin: 20px 0;
}
.challenges__item-summary {
  font-size: 15px;
  margin: 10px 0;
  font-weight: 700;
}
.challengesActions__item {
  margin-bottom: 10px;
}
.challengesActions__item-tools{
  padding: 5px 0;
  padding-left: 25px;
  color: #9c9c9c;
}
.reference-list__title {
  margin-bottom: 10px;
}
.reference-list__list {
  margin-left: 20px;
}
.reference-list__name {
  font-size: 18px;
  margin-bottom: 5px;
}
.reference-list__role {
  font-size: 14px;
  color: #666666;
  font-weight: 100;
}

// Education
.education__list-item {
  margin-bottom: 20px;
}
.education__item-institution {
  color: #5E5E5E;
}
.education__item-study {
  font-weight: 100;
  font-size: 14px;
}
.education__item-date {
  font-weight: 100;
  font-size: 12px;
}
.achievements__list-item {
  font-size: 14px;
  margin-top: 15px;
}




//LanguagesList
.languages__wrapper {
    padding: 10px 0;
}

.languages__title {
    margin: 30px 0;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 700;
}

// LanguageItem
.languages__list-item {
    margin-bottom: 15px;
    margin-left: 10px;
}

.languages__item-language {
    font-size: 18px;
    font-weight: 700;
    color: #5e5e5e;
}

.languages__item-level {
    font-size: 14px;
    font-weight: 100;
}

// MiscEducation
.miscEducation__wrapper {
    padding: 20px 0 40px;
}

.miscEducation__title {
    margin: 20px 0;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 700;
}

.miscEducation__list {
    list-style: square inside;
}

.miscEducation__item {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 20px;
}

// Questionnaire

.questionnaire__item {
  margin-bottom: 20px;
}
.questionnaire__item-question {
  font-size: 18px;
  line-height: 25px;
  margin-bottom: 10px;
  font-weight: 700;
  color: #636363;
}
